import React, { useState } from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import ButtonGroup from '../elements/ButtonGroup';
import Button from '../elements/Button';
import Image from '../elements/Image';
import Modal from '../elements/Modal';

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const Hero = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const [videoModalActive, setVideomodalactive] = useState(false);
  const [pwActive, setPwActive] = useState(false);
  const [loreActive, setLoreActive] = useState(false);
  const [packagesActive, setPackagesActive] = useState(false);

  const openModal = (e) => {
    e.preventDefault();
    setVideomodalactive(true);
  }

  const closeModal = (e) => {
    e.preventDefault();
    setVideomodalactive(false);
  }   

  const openPW = (e) => {
    e.preventDefault();
    setPwActive(true);
  }

  const closePW = (e) => {
    e.preventDefault();
    setPwActive(false);
  }  

  const openLore = (e) => {
    e.preventDefault();
    setLoreActive(true);
  }

  const closeLore = (e) => {
    e.preventDefault();
    setLoreActive(false);
  }  
  
  const openPackages = (e) => {
    e.preventDefault();
    setPackagesActive(true);
  }

  const closePackages = (e) => {
    e.preventDefault();
    setPackagesActive(false);
  }  

  const outerClasses = classNames(
    'hero section center-content',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'hero-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section id="Home"
      {...props}
      className={outerClasses}
    >
      <div className="container-sm">
        <div className={innerClasses}>
          <div className='hero-content'>
            <Image
              className="mb-24"
              src={require('./../../assets/images/logo.svg')}
              alt="Hero"
              width={225}
              height={225}
            />
          </div>
          <div className="hero-content">
            <h1 className="mt-10 mb-8 logo-text">
              Purple World:
              {/* <span className="text-color-primary">RP</span> */}
            </h1>
            <h1 className="mt-0 mb-16 logo-text">
              Once Upon a Time in Cayo
              {/* <span className="text-color-primary">RP</span> */}
            </h1>
            <div className="container-xs">
              <p className="m-0 mb-32">
                {/* The President of Cayo Perico welcomes you.  */}
              </p>
              {/* <p className="m-0 mb-32">
                Closed Alpha - October 2022
                </p> */}
              <div>
                <ButtonGroup>
                <Button tag="a" className="main-button" color="black" onClick={openLore} wideMobile href="https://cruip.com/">
                    Apply Now
                    </Button>
                </ButtonGroup>
              </div>
            </div>
          </div>
          {/* <div className="hero-figure reveal-from-bottom illustration-element-01" data-reveal-value="20px" data-reveal-delay="800">
            <a
              data-video="https://player.vimeo.com/video/174002812"
              href="#0"
              aria-controls="video-modal"
              onClick={openModal}
            >
              <Image
                className="has-shadow"
                src={require('./../../assets/images/video-placeholder.jpg')}
                alt="Hero"
                width={896}
                height={504} />
            </a>
          </div> */}
          <Modal
            id="lore-modal"
            show={loreActive}
            handleClose={closeLore}
            innerContent={
              <> 
              <p className='mt-24 mb-24 modal-p'>
              <Button>Join our discord and apply</Button>
              </p>
              </>
            }
            >
          </Modal>
          <Modal
            id="packages-modal"
            show={packagesActive}
            handleClose={closePackages}
            innerContent={
              <>
              <p className='mb-32 modal-p'>

              </p>
              </>
            }
            >
          </Modal>
          {/* <Modal
            id="video-modal"
            show={videoModalActive}
            handleClose={closeModal}
            video="https://player.vimeo.com/video/174002812"
            videoTag="iframe" /> */}
        </div>
      </div>
    </section>
  );
}

Hero.propTypes = propTypes;
Hero.defaultProps = defaultProps;

export default Hero;