import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import SectionHeader from '../sections/partials/SectionHeader';
import Image from '../elements/Image';
const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const GenericSection = ({
  className,
  children,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const sectionHeader = {
    title: 'Season One'
  };

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          {/* <div className='container-xs'>
          <h2 class="mt-0 mb-16 ta-c-desktop">Setting</h2>
          </div> */}
          <p className='mt-32 mb-32 modal-p'>
          Set in an archipelago off the coast of Panama, a small island with a robust heritage has initiated 
          a repopulation edict intended to bolster its boatmaking, sugar, and tobacco industries.
          </p>
          <Image
          src={require('../../assets/images/map.png')}
          />
          <p className='mt-32 mb-32 modal-p'>
          The island known as Cayo Perico, formerly a military dictatorship steming
          from remenents of the Panama Defense Force, now looks to thrive as
          an internationally recognized republic.
          </p>
          <Image width={450} className='image-center'
          src={require('../../assets/images/map2.png')}
          />
          <p className='mt-32 mb-32 modal-p'>
          Just to the north of Cayo Perico is San Andreas, a trading and military partner with the United States.
          Due to recent increased illegal drug activity and threats of terrorism the United States has dispatched
          Its 12th fleet to aid in enforcing maritime law in accordance with the San Andreas Maritime Delimitation Treaty.
          </p>
          {children}
        </div>
      </div>
    </section>
  );
}

GenericSection.propTypes = propTypes;
GenericSection.defaultProps = defaultProps;

export default GenericSection;